import {
    Avatar,
    Button,
    Card,
    CardActions,
    CardContent,
    CardHeader,
    CardMedia,
    Container,
    Grid2,
    List,
    ListItemButton,
    ListItemIcon,
    ListItemText,
    Paper,
    Typography
} from '@mui/material';
import React, {useContext} from 'react';
import PageWrapper from "../components/PageWrapper";
import {AppContext, TAppContext} from "../context/AppContext";
import girl_image from '../assets/images/icons/girl_icon.png';
import {useSelector} from "react-redux";
import {RootStateType} from "../redux/store/ConfigureStore";
import {ReduxSliceImageInterface} from "../redux/slice/ImageSlice";
import {Palette} from "@mui/icons-material";
import {useNavigate} from "react-router-dom";
import {imageType} from "../types/ApiTypes";
import {ReduxSliceUserInterface} from "../redux/slice/UserSlice";
import vipLogo from "../assets/images/vip_logo.png";
import {orange} from '@mui/material/colors';
import TabsBarImages from "../components/TabsBarImages";

const HomePage: React.FC = () => {
    const {lang} = useContext<TAppContext>(AppContext);
    const {popularImageTypes, favoriteImageTypes} = useSelector<RootStateType, ReduxSliceImageInterface>(state => state.image)
    const navigate = useNavigate();
    const {userDbData} = useSelector<RootStateType, ReduxSliceUserInterface>(state => state.user)

    const openImageType = (imageTypeItem: imageType) => {
        if (imageTypeItem.vip && !userDbData?.is_vip) {
            // vip
        } else {
            navigate('/generate/select-default-image/' + imageTypeItem.id)
        }
    }

    return (
        <React.Fragment>
            <PageWrapper title={lang.HEADERS.MAIN_PANEL}>
                <Card square elevation={3}>
                    <CardHeader
                        avatar={
                            <Avatar
                                src={girl_image}
                                variant="rounded" />
                        }
                        title={lang.HEADERS.HOME_PANEL}
                        subheader={lang.DESCRIPTIONS.HOME_PANEL_TOP}
                    />
                    <CardActions disableSpacing>
                        <Button variant="contained">{lang.BUTTONS.HOME_PANEL_GO}</Button>
                    </CardActions>
                </Card>
                {
                    !!favoriteImageTypes.length &&
                        <Paper square elevation={3}>
                            <CardHeader title={lang.TITLES.HOME_PANEL_FAVORITE} />
                            <TabsBarImages images={favoriteImageTypes} />
                        </Paper>
                }
                <List
                    sx={{ width: '100%', bgcolor: 'background.paper' }}
                    component="div"
                    aria-labelledby="nested-list-subheader"
                >
                    <Container>
                        <Typography color="primary" variant="h6" component="div">
                            {lang.TITLES.HOME_PANEL_POPULAR}
                        </Typography>
                    </Container>
                    {
                        !!popularImageTypes.length && popularImageTypes.map((value, key) =>
                            <ListItemButton key={key}>
                                <ListItemIcon onClick={() => openImageType(value)}>
                                    <Palette />
                                </ListItemIcon>
                                <ListItemText secondary={value.name} />
                            </ListItemButton>
                        )
                    }
                </List>
                {
                    !userDbData?.is_vip &&
                        <Paper square className="vip-block pulseLine" sx={{pb: 1, background: 'var(--black_gradient)'}} elevation={3}>
                            <CardMedia
                                component="img"
                                height="auto"
                                image={vipLogo}
                                alt="Paella dish"
                            />
                            <CardContent sx={{py: 1}}>
                                <Grid2 container spacing={2}>
                                    <Grid2 size={6}>
                                        <Typography
                                            component="div"
                                            variant="h2" sx={{fontFamily: 'FloridaVibesByTurbologo, serif', color: orange[500], textAlign: 'center'}}>
                                            Renestra
                                        </Typography>
                                    </Grid2>
                                    <Grid2 size={6}>
                                        <Typography
                                            color={orange[200]}
                                            component="div"
                                            variant="subtitle2" >
                                            {lang.DESCRIPTIONS.VIP_BLOCK_1}
                                        </Typography>
                                        <Typography
                                            color={orange[300]}
                                            component="div"
                                            variant="subtitle2" >
                                            {lang.DESCRIPTIONS.VIP_BLOCK_2}
                                        </Typography>
                                        <Typography
                                            color={orange[400]}
                                            component="div"
                                            variant="subtitle2" >
                                            {lang.DESCRIPTIONS.VIP_BLOCK_3}
                                        </Typography>
                                    </Grid2>
                                </Grid2>
                            </CardContent>
                            <CardHeader
                                sx={{py: 0}}
                                title={lang.HEADERS.HOME_PANEL}
                                subheader={lang.DESCRIPTIONS.HOME_PANEL_TOP}
                                avatar={<Typography sx={{px: 1, borderRadius: 1, color: orange[800], background: 'var(--gold_gradient)'}} >VIP</Typography>}
                            />
                            <CardActions>
                                <Button color='inherit' fullWidth sx={{background: 'var(--gold_gradient)', mx: 1}}>
                                    {lang.TITLES.VIP_BLOCK_GOLD_BUTTON}
                                </Button>
                            </CardActions>
                        </Paper>
                }
            </PageWrapper>
        </React.Fragment>
    );
};

export default HomePage;
