import {
    Avatar,
    Button,
    Card,
    CardActions,
    CardContent,
    CardHeader,
    CardMedia,
    Grid2,
    Link,
    Paper,
    Typography
} from '@mui/material';
import React, {useContext} from 'react';
import PageWrapper from "../components/PageWrapper";
import {AppContext, TAppContext} from "../context/AppContext";
import girl_image from '../assets/images/icons/girl_icon.png';
import {useSelector} from "react-redux";
import {RootStateType} from "../redux/store/ConfigureStore";
import {ReduxSliceUserInterface} from "../redux/slice/UserSlice";
import vipLogo from "../assets/images/vip_logo.png";
import {orange} from '@mui/material/colors';
import {UrlConstants} from "../constants/UrlConstants";

const AboutPage: React.FC = () => {
    const {lang} = useContext<TAppContext>(AppContext);
    const {userDbData} = useSelector<RootStateType, ReduxSliceUserInterface>(state => state.user)

    return (
        <React.Fragment>
            <PageWrapper title={lang.HEADERS.ABOUT_PANEL}>
                <Card square elevation={3}>
                    <CardHeader
                        avatar={
                            <Avatar
                                src={girl_image}
                                variant="rounded" />
                        }
                        title={lang.TITLES.ABOUT_PANEL_APP_NAME}
                        subheader={lang.TITLES.ABOUT_PANEL_SUBTITLE}
                    />
                    <CardActions>
                        <Typography color="textSecondary" variant="caption" component="div">
                            {lang.DESCRIPTIONS.ABOUT_PANEL_AI_DESCRIPTION}<br />{lang.DESCRIPTIONS.ABOUT_PANEL_POLITIC_START} {" "}
                            <Link underline="none" target='_blank' href={UrlConstants.URL_POLITIC}>{lang.DESCRIPTIONS.ABOUT_PANEL_POLITIC}</Link>
                            {" "}  {" "}
                            <Link underline="none" target='_blank' href={UrlConstants.URL_RULE_APP}>{lang.DESCRIPTIONS.ABOUT_PANEL_RULES}</Link>.
                        </Typography>
                    </CardActions>
                </Card>
                {
                    !userDbData?.is_vip &&
                    <Paper square className="vip-block pulseLine" sx={{my: 2, background: 'var(--black_gradient)'}} elevation={3}>
                        <CardMedia
                            component="img"
                            height="auto"
                            image={vipLogo}
                            alt="Paella dish"
                        />
                        <CardContent sx={{py: 1}}>
                            <Grid2 container spacing={2}>
                                <Grid2 size={6}>
                                    <Typography
                                        component="div"
                                        variant="h2" sx={{fontFamily: 'FloridaVibesByTurbologo, serif', color: orange[500], textAlign: 'center'}}>
                                        Renestra
                                    </Typography>
                                </Grid2>
                                <Grid2 size={6}>
                                    <Typography
                                        color={orange[200]}
                                        component="div"
                                        variant="subtitle2" >
                                        {lang.DESCRIPTIONS.VIP_BLOCK_1}
                                    </Typography>
                                    <Typography
                                        color={orange[300]}
                                        component="div"
                                        variant="subtitle2" >
                                        {lang.DESCRIPTIONS.VIP_BLOCK_2}
                                    </Typography>
                                    <Typography
                                        color={orange[400]}
                                        component="div"
                                        variant="subtitle2" >
                                        {lang.DESCRIPTIONS.VIP_BLOCK_3}
                                    </Typography>
                                </Grid2>
                            </Grid2>
                        </CardContent>
                        <CardActions>
                            <Button color='inherit' fullWidth sx={{background: 'var(--gold_gradient)', mx: 1}}>
                                {lang.TITLES.VIP_BLOCK_GOLD_BUTTON}
                            </Button>
                        </CardActions>
                    </Paper>
                }
            </PageWrapper>
        </React.Fragment>
    );
};

export default AboutPage;
